.search-bar-container{max-width:400px;display:block;margin:100px}.player-search-input{height:30px;width:300px;border-radius:3px;position:relative;box-shadow:3px 3px 3px 3px}.player-search-input.active .search-bar-results{opacity:1}#search-bar{box-sizing:border-box;height:50px;width:300px;padding:15px 15px;border-radius:5px;outline:none}.search-bar-results{padding-top:15px;padding-bottom:15px;max-height:180px;overflow-y:hidden;overflow:hidden;border-left:1px solid;border-bottom:1px solid;border-right:1px solid;background:white;box-sizing:border-box;opacity:0}.search-bar-results li{list-style:none;padding:10px 25px;width:100%;cursor:default;border-radius:2px;z-index:3;margin-bottom:2px;box-sizing:border-box}.search-bar-results li:hover{background:rgba(221,220,220,0.828)}
